"use client";
import React, { useEffect } from "react";

// Define types for the Google Translate API
declare global {
  interface Window {
    googleTranslateElementInit: () => void;
    google: {
      translate: {
        TranslateElement: {
          new (options: {
            pageLanguage: string;
            includedLanguages: string;
            layout: any;
          }, elementId: string): void;
          InlineLayout: {
            SIMPLE: any;
          };
        };
      };
    };
  }
}

const GTranslate: React.FC = () => {
  useEffect(() => {
    const addGoogleTranslateScript = () => {
      if (!document.querySelector("#google-translate-script")) {
        const script = document.createElement("script");
        script.id = "google-translate-script";
        script.src = "https://translate.google.com/translate_a/element.js?cb=googleTranslateElementInit";
        script.async = true;
        document.body.appendChild(script);
      }
    };
    
    window.googleTranslateElementInit = () => {
      new window.google.translate.TranslateElement(
        {
          pageLanguage: "en", // Default language
          includedLanguages: "zh-CN,zh-TW,en,sd,hi,gu,mr,ta,fr,ja,de,ru,en,hi", // Added both simplified and traditional Chinese
          layout: window.google.translate.TranslateElement.InlineLayout.SIMPLE,
        },
        "google_translate_element"
      );
    };
    
    addGoogleTranslateScript();
    
    // Clean up script on component unmount
    return () => {
      const script = document.querySelector("#google-translate-script");
      if (script) {
        script.remove();
      }
      delete window.googleTranslateElementInit;
    };
  }, []);
  
  return (
    <div
      id="google_translate_element"
      className="fixed bottom-4 right-1 z-50 bg-white bg-opacity-20 hover:bg-opacity-100 rounded-md p-2 transition-opacity duration-300 shadow-sm hover:shadow-md"
      style={{ opacity: 0.2, transition: "opacity 0.3s ease" }}
      onMouseEnter={(e) => {
        e.currentTarget.style.opacity = "1";
      }}
      onMouseLeave={(e) => {
        e.currentTarget.style.opacity = "0.2";
      }}
    ></div>
  );
};

export default GTranslate;